<template>
  <div class="modal fade show" id="modal-default" style="padding-right: 16px; display: block;" aria-modal="true" role="dialog" v-if="show">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title">用户信息</h4>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true" @click="show = false">×</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="card-body box-profile">
            <div class="text-center">
              <VueUploadImgs multiple compress :before-read="beforeRead" :after-read="afterRead" :before-remove="beforeRemove" :limit="limit" :type="type" @preview="preview" @exceed="exceed" @oversize="oversize" v-model="files">
              </VueUploadImgs>
            </div>
            <p class="text-muted text-center">{{tip}}</p>
            <form>
              <formly-form :form="form" :model="model" :fields="fields" ref="credentials"></formly-form>
            </form>
            <a href="#" class="btn btn-primary btn-block" @click="formValid()"><b>提交</b></a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/server/api";

export default {
  name: "userinfo",
  inject: ["reload"],
  props: {
    model: {
      type: Object,
      default: {},
    },
    form: {
      type: Object,
      default: {},
    },
    fields: {
      type: Array,
      default: [],
    },
    api: {
      type: String,
      default: "",
    },
  },
  watch: {
    model: function (n, o) {
      this.show = true;
      var mods = this.api.split(".");
      this.method = mods[0];
      this.apiObj = API[mods[1]][mods[2]];
      this.files = [{ url: n.face_img, name: n.img_name }];
      this.tip = null;
    },
  },
  data() {
    return {
      show: false,
      apiObj: null,
      method: null,

      tip: "请上传正脸照片",

      // 图片
      limit: 1,
      files: [],
      type: 0, // 0 预览模式 1 列表模式 2 预览模式 + 上传按钮
      previewIMG: null,
      isPreview: false,
      maxSize: 1024 * 2000, // 2MB
    };
  },
  methods: {
    formValid() {
      this.$refs.credentials
        .validate()
        .then(() => {
          if (!this.form.$valid) return;
          this.submit();
        })
        .catch((e) => {});
    },
    submit() {
      this.show = false;
      var formData = JSON.parse(JSON.stringify(this.model));

      if (this.files[0].url.startsWith("data:image/")) {
        formData["img_data"] = this.files[0].url.replace(
          /^data:image\/\w+;base64,/,
          ""
        );
      }
      if (this.method == "post") {
        this.$req.post(this.apiObj.path, formData).then((resp) => {
          if (resp.code == 1) {
            this.reload();
          }
        });
      } else {
        this.$req
          .fetch(this.apiObj.path, formData, { successNotify: true })
          .then((res) => {
            if (resp.code == 1) {
              this.reload();
            }
          });
      }
    },

    // 图片
    oversize(file) {
      console.log("filesize:" + file.size / 1024 + "KB");
    },
    afterRead(files) {
      this.files = files;
    },
    beforeRemove(index, file) {
      return true;
    },
    preview(index, file) {
      // this.previewIMG = file.url;
      // this.isPreview = true;
    },
    exceed() {
      alert(`只能上传${this.limit}张图片`);
    },
    beforeRead(files) {
      for (let i = 0, len = files.length; i < len; i++) {
        const file = files[i];
        if (file.type != "image/jpeg" && file.type != "image/png") {
          alert("只能上传jpg和png格式的图片");
          return false;
        }
      }

      return true;
    },
    closePreview() {
      this.isPreview = false;
    },

    setOptions(label, placeholder, type) {
      return {
        label: label,
        atts: {
          type: type,
          placeholder: placeholder,
        },
        onBlur: function (e) {
          let isOK = !Boolean(this.form.$errors[this.field.key].valCheck);
          if (isOK) {
            this.field.templateOptions.classes = {
              "is-valid": true,
              "is-warning": false,
              "is-invalid": false,
            };
          } else {
            this.field.templateOptions.classes = {
              "is-valid": false,
              "is-warning": false,
              "is-invalid": true,
            };
          }
        },
        onFocus: function () {
          this.field.templateOptions.classes = {
            "is-valid": false,
            "is-warning": true,
            "is-invalid": false,
          };
        },
      };
    },
  },
};
</script>


<style scoped lang="scss">
</style>