import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const Login = () => import('@/pages/Login')

import Index from '@/Index'
import OrganizationMgr from '@/views/organization/OrganizationMgr'

const UserMgr = () => import('@/views/user/UserMgr')

const Dashboard = () => import('@/pages/Dashboard')
const AddFAce = () => import('@/pages/AddFAce')
// const AppointmentQRcode = () => import('@/pages/AppointmentQRcode')


const routes = [{
    path: '',
    redirect: '/login',
  },
  {
    path: '/login',
    component: Login,
  },
  {
    path: '/addface',
    component: AddFAce,
  },
  // {
  //   path: '/qrcode',
  //   component: AppointmentQRcode,
  // },
  {
    path: '/dashboard',
    component: Dashboard,
  },
  {
    path: '/user',
    component: Index,
    children: [{
      path: 'usermgr',
      name: 'usermgr',
      component: UserMgr,
    }, ],
  },
  {
    path: '/organization',
    component: Index,
    children: [{
      path: 'organizationmgr',
      name: 'organizationmgr',
      component: OrganizationMgr,
    }, ],
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
})

// 导航守卫
import Loading from '@/components/loading'
import NProgress from 'nprogress'

router.beforeEach((to, from, next) => {
  if (to.path != '/login' && sessionStorage.getItem('adminInfo') == null) {
    next({
      path: '/login'
    })
  }
  next()
})

router.afterEach(() => {
  // 避免接口一直未响应，跳转路由后还在 loading 的状况出现
  NProgress.done()
  Loading.done()
})

export default router