function OnfocusStyle(_this) {
  _this.field.templateOptions.classes = {
    "is-valid": false,
    "is-warning": true,
    "is-invalid": false,
  }
}

function onBlurStyle(_this) {
  // 若 hasError 
  let isOK = !Boolean(_this.form.$errors[_this.field.key].valCheck)
  if (isOK) {
    // 验证成功
    _this.field.templateOptions.classes = {
      "is-valid": true,
      "is-warning": false,
      "is-invalid": false,
    }
  } else {
    // 验证失败
    _this.field.templateOptions.classes = {
      "is-valid": false,
      "is-warning": false,
      "is-invalid": true,
    }
  }
}

function setOptions(label, placeholder, type) {
  return {
    label: label,
    atts: {
      placeholder: placeholder,
      type: type
    },
    onBlur: function (e) {
      onBlurStyle(this)
    },
    onFocus: function () {
      OnfocusStyle(this)
    }
  }
}