<template>
  <div class="wrapper">
    <!-- modal -->
    <div class="modal fade show" id="modal-default" style="padding-right: 16px; display: block;" aria-modal="true" role="dialog" v-if="showLogoutModal">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">退出登陆</h4>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true" @click="show = false">×</span>
            </button>
          </div>
          <div class="modal-body">
            <p>确定要退出登录吗？</p>
          </div>
          <div class="modal-footer justify-content-between">
            <button type="button" class="btn btn-default" data-dismiss="modal" @click="showLogoutModal=false">取消</button>
            <button type="button" class="btn btn-primary" @click="logout()">确定</button>
          </div>
        </div>
      </div>
    </div>
    <admininfo-modal :api="adminModalApi" :model="adminModalModel" :fields="adminModalFields" :form='adminModalForm' />
    <!-- modal -->

    <!-- Navbar -->
    <nav class="main-header navbar navbar-expand navbar-white navbar-light">
      <!-- Left navbar links -->
      <ul class="navbar-nav">
        <li class="nav-item">
          <a class="nav-link" data-widget="pushmenu" href="#" role="button"><i class="fas fa-bars"></i></a>
        </li>
      </ul>

      <!-- Right navbar links -->
      <ul class="navbar-nav ml-auto">
      </ul>

      <ul class="navbar-nav ml-auto">
        <li class="nav-item dropdown">
          <div class="user-panel d-flex" data-toggle="dropdown">
            <div class="image">
              <!-- <img :src="adminInfo.head_img==''?'assets/img/noBody.png':adminInfo.head_img" class="img-circle elevation-2 mt-1" style="width:26px;height:26px"> -->
              <img src="~@/assets/img/noBody.png" class="img-circle elevation-2 mt-1" style="width:26px;height:26px">
            </div>
            <div class="info">
              <p href="#" class="d-block">{{adminInfo.username}}</p>
            </div>
          </div>

          <div class="dropdown-menu dropdown-menu-lg dropdown-menu-right">
            <div class="dropdown-divider"></div>
            <a href="#" class="dropdown-item" @click="initAdminModal('post.admin.update', 'Admin', {
                        id: adminInfo.id,
                        username: adminInfo.username,
                        img_path: adminInfo.head_img,
                        img_name: adminInfo.username,
                        role: '基础管理员',
                        create_time: adminInfo.create_time,
                      })">
              <i class="fas fa-users mr-2"></i>个人信息

            </a>
            <div class="dropdown-divider"></div>
            <!-- <a href="#" class="dropdown-item">
              <i class="fas fa-file mr-2"></i>修改密码
            </a> -->
            <div class="dropdown-divider"></div>
            <a href="#" class="dropdown-item dropdown-footer" @click="showLogoutModal=true">退出登陆</a>
          </div>
        </li>
      </ul>
    </nav>

    <aside class="main-sidebar sidebar-dark-primary elevation-4">
      <a href="" class="brand-link text-center">
        <span class="brand-text font-weight-light">智能考勤系统</span>
      </a>
      <div class="sidebar">
        <nav class="mt-2">
          <ul class="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
            <li class="nav-item has-treeview menu-open">
              <ul class="nav nav-treeview">
                <li class="nav-item">
                  <router-link to="/dashboard" class="nav-link">
                    <i class="far fa-circle nav-icon"></i>
                    <p>数据展板</p>
                  </router-link>
                </li>
              </ul>
            </li>
          </ul>

          <ul class="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
            <li class="nav-item has-treeview menu-open">
              <ul class="nav nav-treeview">
                <li class="nav-item">
                  <router-link to="/organization/organizationmgr" class="nav-link">
                    <i class="far fa-circle nav-icon"></i>
                    <p>班级管理</p>
                  </router-link>
                </li>
              </ul>
            </li>
          </ul>

          <ul class="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
            <li class="nav-item has-treeview menu-open">
              <ul class="nav nav-treeview">
                <li class="nav-item">
                  <router-link to="/user/usermgr" class="nav-link">
                    <i class="far fa-circle nav-icon"></i>
                    <p>人脸管理</p>
                  </router-link>
                </li>
              </ul>
            </li>
          </ul>

          <ul class="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
            <li class="nav-item has-treeview menu-open">
              <ul class="nav nav-treeview">
                <li class="nav-item">
                  <router-link to="/addface" class="nav-link">
                    <i class="far fa-circle nav-icon"></i>
                    <p>添加人脸</p>
                  </router-link>
                </li>
              </ul>
            </li>
          </ul>

        </nav>
      </div>
    </aside>

    <!-- Content Wrapper. Contains page content -->
    <div class="content-wrapper">
      <v-formly :modalOptions="modalOptions"></v-formly>
      <router-view @sendModaloption="sendModaloption" />
    </div>

    <!-- Control Sidebar -->
    <aside class="control-sidebar control-sidebar-dark">
      <!-- Control sidebar content goes here -->
      <div class="p-3">
        <h5>Title</h5>
        <p>Sidebar content</p>
      </div>
    </aside>
    <!-- /.control-sidebar -->

    <!-- Main Footer -->
  </div>
</template>

<script>
import API from "@/server/api";
import FormFields from "@/plugins/form-fields";
import VFormly from "@/components/modal/VFormly";
import AdmininfoModal from "@/components/modal/UserInfo";

export default {
  data() {
    return {
      showLogoutModal: false,
      adminModalApi: null,
      adminModalModel: null,
      adminModalForm: {},
      adminModalFields: null,

      adminInfo: null,
      modalOptions: null,
    };
  },
  components: {
    VFormly,
    AdmininfoModal,
  },
  created() {
    this.adminInfo = JSON.parse(sessionStorage.getItem("adminInfo"));
  },
  methods: {
    initAdminModal: function (api, fieldService, model) {
      this.adminModalApi = api;
      this.adminModalModel = model;
      // fields
      if (fieldService != undefined) {
        this.adminModalFields = FormFields[fieldService]();
      } else {
        this.adminModalFields = [];
      }
    },
    sendModaloption: function (opts) {
      this.modalOptions = opts;
    },
    logout: function () {
      this.$req
        .fetch(
          API.admin.logout.path,
          {},
          {
            successNotify: true,
          }
        )
        .then((resp) => {
          if (resp.code == 1) {
            sessionStorage.removeItem("token");
            sessionStorage.removeItem("adminInfo");
            this.$router.push("/login");
          }
        });
    },
  },
};
</script>

<style lang="scss">
.modal-li {
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
}
.modal {
  background-color: rgba(0, 0, 0, 0.8);
  .modal-dialog {
    top: 5%;
  }
}
.user-panel {
  cursor: pointer;
}
</style>
