      <div class="modal fade show" id="modal-default" style="padding-right: 16px; display: block;" aria-modal="true" role="dialog" v-show="show">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title">新增班级</h4>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true" @click="show = false">×</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="form-group">
                <label>班级名称</label>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <span class="input-group-text"><i class="far fa-clock"></i></span>
                  </div>
                  <input type="text" class="form-control float-right" v-model="organizationName" />
                </div>
              </div>
              <div class="form-group">
                <label>任课教师</label>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <span class="input-group-text"><i class="far fa-clock"></i></span>
                  </div>
                  <input type="text" class="form-control float-right" v-model="organizationLeader" />
                </div>
              </div>
              <div class="form-group">
                <label>考勤时间</label>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <span class="input-group-text"><i class="far fa-clock"></i></span>
                  </div>
                  <input type="text" class="form-control float-right" autocomplete="off" ref="rangetime" id="rangetime" v-model="timeRange">
                </div>
              </div>
            </div>
            <div class="modal-footer justify-content-between">
              <button type="button" class="btn btn-default" data-dismiss="modal" @click="show=false">取消</button>
              <button type="button" class="btn btn-primary" @click="AddOrganization">确认</button>
            </div>
          </div>
        </div>
      </div>

      <!-- modal -->
      <div class="row">
        <div class="col-12">
          <div class="card">
            <div class="card-header">
              <h3 class="card-title">班级列表</h3>
              <div class="card-tools">
                <div class="input-group input-group-sm">
                  <input type="text" name="table_search" class="form-control float-right" placeholder="Search">
                  <div class="input-group-append">
                    <button type="submit" class="btn btn-default">
                      <i class="fas fa-search"></i>
                    </button>
                    <button type="button" class="btn btn-block btn-primary ml-3" @click="show=true">新增班级
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div class="card-body table-responsive p-0" style="height: 100%;">
              <table class="table table-head-fixed text-nowrap table-striped projects">
                <thead>
                  <tr class="text-center">
                    <th>班级名称</th>
                    <th>任课教师</th>
                    <th>开始时间</th>
                    <th>结束时间</th>
                    <th>邀请码</th>
                    <th>操作</th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="text-center" v-for="item in organizations" :key="item.id">
                    <td class="project_progress">
                      {{ item.name }}
                    </td>
                    <td class="project_progress">
                      {{ item.leader }}
                    </td>
                    <td class="project_progress">
                      {{ item.start_time }}
                    </td>
                    <td class="project_progress">
                      {{ item.end_time }}
                    </td>
                    <td class="project_progress">
                      {{ item.invite_code }}
                    </td>
                    <td class="project-actions">
                      <a class="btn btn-info btn-sm" href="#" @click="
                      $emit('sendModaloption', {
                        modalType: 'form',
                        title: '修改班级信息',
                        apiObj: 'post.organization.update',
                        model: {
                          id: item.id,
                          name: item.name,
                          leader: item.leader,
                          invite_code: item.invite_code,
                          start_time: item.start_time,
                          end_time: item.end_time,
                        },
                        fieldService: 'OrganizationUpdateFields',
                      })
                    ">
                        <i class="fas fa-pencil-alt"></i>
                        修改
                      </a>
                      <a class="btn btn-danger btn-sm" href="#" @click="
                      $emit('sendModaloption', {
                        title: '警告',
                        apiObj: 'post.organization.delete',
                        model: {
                          pid: item.id,
                        },
                        tipText: '确定要删除吗？',
                      })
                    ">
                        <i class="fas fa-trash"></i>
                        删除
                      </a>
                    </td>
                  </tr>

                </tbody>

              </table>
            </div>
            <div class="card-footer clearfix">
              <!-- <Pagination :page-index="currentPage" :total="userCount" :page-size="pageSize" @change="pageChange" /> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import API from "@/server/api";
import { Notification } from "element-ui";

export default {
  inject: ["reload"],
  components: {},
  data() {
    return {
      show: false,

      organizationName: "",
      organizationLeader: "",
      timeRange: "09:00 AM - 06:00 PM",
      endTime: null,
      organizations: null,
    };
  },
  mounted() {
    this.getOrganizations();
  },
  methods: {
    vaildAddOrgForm() {
      var reg = /^[^*/\\?]+$/g;
      if (this.organizationName.length == 0) {
        return "班级名称不能为空";
      }
      if (this.organizationLeader.length == 0) {
        return "任职教师不能为空";
      }
      return true;
    },
    AddOrganization() {
      this.show = false;

      var res = this.vaildAddOrgForm();
      if (res != true) {
        Notification({
          title: res || "输入数据有误，请仔细检测",
          type: "warning",
        });
        return;
      }

      var ranegArray = this.timeRange.split(" - ");
      var startString = ranegArray[0]; // 09:00 AM
      var endString = ranegArray[1];

      var startTime = startString.split(" ")[0];
      var endTime = endString.split(" ")[0];

      if (startString.split(" ")[1] == "PM") {
        let ls = startString.split(" ")[0].split(":");
        var hour24 =
          String(Number(ls[0]) + 12) == 24 ? "00" : String(Number(ls[0]) + 12);
        startTime = hour24 + ":" + ls[1];
      }
      if (endString.split(" ")[1] == "PM") {
        let ls = endString.split(" ")[0].split(":");
        var hour24 =
          String(Number(ls[0]) + 12) == 24 ? "00" : String(Number(ls[0]) + 12);
        endTime = hour24 + ":" + ls[1];
      }

      this.$req
        .post(API.organization.add.path, {
          organizationName: this.organizationName,
          organizationLeader: this.organizationLeader,
          startTime: startTime,
          endTime: endTime,
        })
        .then((resp) => {
          if (resp.code == 1) {
            this.reload();
          }
        });
    },
    getOrganizations() {
      this.$req.fetch(API.organization.list.path, {}).then((resp) => {
        if (resp.code == 1) {
          this.organizations = resp.data;
        }
      });
    },
    jobPageChange(page) {
      this.jobCurrentPage = page;
      this.getJobs();
    },
    logPageChange(page) {
      this.logCurrentPage = page;
      this.getLogs(this.jobName);
    },
    toggleJobs(reqTyp) {
      this.reqTyp = reqTyp;
      this.getJobs();
    },
  },
};
</script>

